import React from "react";
import './member.css';
//스타일선택자를 클래스로 사용함-> 중복으로 적용을 방지하기 위해서
//각각의 독립된 스타일 선언시 파일명.module.css로 저장을 하고
//import styles from './파일명.module.css'  로 선언 후
//적용할 때는 className={styles.mainimg}


function Member(){
    return(
        <div id="sub_member">
            <div id="sub_member_in">
                <div className="location"></div>
                <div className="membership_img"></div>
                <div className="benefit bf01">
                    <div className="bf_number">혜택1</div>
                    <h5>웰컴 기프트로 가입비 돌려받자!</h5>
                    <h6>가입비 내신 만큼 돌려드립니다 (택1)</h6>
                    <ul className="bf_list">
                        <li>
                            <img src="" alt="" />
                            <p>모성이 빛나는 순간 아이엠마더</p>
                        </li>
                        <li>
                            <img src="" alt="" />
                            <p>민감한 아이를 위한 아이엠마더 컴포트케어</p>
                        </li>
                        <li>
                            <img src="" alt="" />
                            <p>임페리얼드림XO</p>
                        </li>
                        <li>
                            <img src="" alt="" />
                            <p>365일 자연방목 유기농 산양유아식</p>
                        </li>
                    </ul>
                    <p>가입선물은 <span>유료가입자</span>에 한하여 지급되며 선택한 사은품은 변경 및 교환이 불가합니다</p>
                </div>
                <div className="benefit bf02"></div>
            </div>
        </div>
    )
}

export default Member;