function ProductOneBox({product}){
    return(
        <div>
            <div className={styles.product_one_box}>
                <img src={product.image} alt="product_image" />
                <p className={styles.name}>{product.name}</p>
                <p className={styles.price}><span>{product.price}</span>원</p>
            </div>
        </div>
    )
}

